import React from "react";
import axios from "./axios";
import { useState, useEffect } from "react";
import "./index.css";
import YouTube from "react-youtube";
import movieTrailer from "movie-trailer";
const base_url = "https://image.tmdb.org/t/p/original/";

function Row({ title, fetchUrl, isLargeRow = false }) {
  const [movies, setMovies] = useState([]);
  const [trailerUrl, setTrailerUrl] = useState("");

  //A snippet of code that runs based on a specific condition every time the Row loads this snippet will run
  useEffect(() => {
    async function fetchData() {
      const request = await axios.get(fetchUrl);
      setMovies(request.data.results);
      return request;
    }
    fetchData();
  }, [fetchUrl]); //[]  runs only once , if some param was given like [movies] then it would run evrytime movie changes

  const opts = {
    height: "390",
    width: "640",
    playerVars: {
      // https://developers.google.com/youtube/player_parameters
      autoplay: 1,
    },
  };
  const handleClick = (movie) => {
    console.log(movie);
    if (trailerUrl !== "") {
      setTrailerUrl("");
    } else {
      movieTrailer(null, { tmdbId: movie?.id })
        .then((url) => {
          console.log(url);
          const urlParams = new URLSearchParams(new URL(url).search);
          setTrailerUrl(urlParams.get("v"));
        })
        .catch((error) => console.log(error));
    }
  };
  return (
    <div className="row">
      <h2 className="row_title">{title}</h2>

      <div className="row_posters">
        {movies.map((movie) => {
          return (
            <img
              key={movie.id}
              className={`${isLargeRow && "row_posterLarge"}`}
              onClick={() => handleClick(movie)}
              src={`${base_url}${
                isLargeRow ? movie.poster_path : movie.backdrop_path
              }`}
              alt={`${movie.original_title}`}
            />
          );
        })}
      </div>
      {trailerUrl && (
        <YouTube videoId={trailerUrl} opts={opts} className="trailerVideo" />
      )}
    </div>
  );
}

export default Row;
